/**
Synapse - adaptive theme
.col-xs- <544px Extra small
.col-sm- ≥544px Small
.col-md- ≥768px Medium
.col-lg- ≥992px Large
.col-xl- ≥1200px Extra large
Gutter width	1.875rem / 30px (15px on each side of a column)
*/

.region-tabs {
  height: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  position: relative;
  width: auto;
  font-size: 0.875em;
  line-height: 1.1em;
  nav {
    position: absolute;
    top:0;
    right: 0;
    float: right;
    z-index: 8;
    padding: 0 0 0 0.1em;
    margin: 0;
    overflow: hidden;
    border: none;
    background: none;
  }
}
/** primary */
.tabs {
  &.primary {
    li {
      display: block;
      float: left;
      vertical-align: bottom;
      margin: 0 0 0 0.35em;
      a {
        color: #000;
        opacity:0.65;
        background-color: #efefef;
        height: 1.8em;
        line-height: 1.8em;
        display: block;
        float: left;
        padding: 0 0.4em;
        margin: 0;
        border: solid 1px #9ab1df;
        text-shadow: 0 1px 0 #fff;
        border-radius: 4px;
        &:hover {
          opacity:1;
        }
      }
      &.active {
        a {
          background-color: #ffffff;
        }
      }
    }
  }
  ul.secondary {
    position: absolute;
    top:25px;
    right: 0;
    float: right;
    z-index: 8;
    padding: 0 0 0 3px;
    margin: 0;
    overflow: hidden;
    border: none;
    background: none;
  }
  li {
    display: block;
    float: left;
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      border-right: none;
    }
    a {
      padding: 0.25em 0.5em;
      &.active {
        background: #f2f2f2;
        border-radius: 5px;
      }
    }
  }
}
